import * as React from 'react';
import { object } from 'prop-types';

import './Project.scss'

interface Props{
    num: number
}

export const Project = (prop:Props) => {
    const projects = [
        ["Tierrita","Tierrita es un proyecto que realizado con el objetivo de resaltar la belleza de una de los lugares ms icnicos de mi pueblito llamado Acienda los Indios, de Pradera, Valle. Un lugar que tiene historias de libertadores, guerras y amorios como los Simn Bolivar y sus pares.",{img:['01','02','03','04','05','06','07','08','09']}, 'Muchas gracias'],
        null,
        ["Nukak","Nukak es una pgina web la cual vende objetos desarrollados por diseadores que estn basados en las artesanas de sus culturas. El desarrollo de esta pgina cuenta con servicio de compras adems de tener tambin un diseo responsive.",{img:['05']},'Muchas gracias'],
        ['Amnistia',"Amnista internacional es una de las ONG defensora de los derechos humanos ms importantes del mundo. El objetivo de esta revista fue resaltar por lo que este grupo de defensores lucha, ama y defienda. Respetar los derechos huamnos, demostrar la lucha que hacen para eliminar las injusticias que las personas sufren. Se quiso representar el dolor sentido por las victimas que han vivido o siguen viviendo este tipo de injusticias, por esta razn su portada es una muestra de muchas de las injusticias que viven las personas, un collage de abuso. Abuso contra las mujeres, contra los nios, contra cualquier persona",{img:['1','2','3','4']},'Muchas gracias'],
        null,
        null
    ];
    return <div className="project">
    <div className="items">
    {projects[prop.num].map((e, index)=>{
        if(index == 1)
        return <p>{e}</p>
        else if(typeof(e) === 'object')  return e.img.map(a=> <img src={"./assets/img/"+ projects[prop.num][0] +"/"+a+".jpg"}></img>);else return <h1>{e}</h1>;
    })}
    </div>
    </div>;
}