import * as React from 'react';

import './Grid.scss';

const projects = [{
    name: 'Tierrita',
    desc: 'Hacienda los Indios. Pradera, Valle. Resalta la belleza de uno de los patrimonios culturales de uno de los pueblitos ms hermosos de Colombia',
    img: 'Tierrita.jpg',
    link: 'https://www.behance.net/gallery/37719507/Welcome-to-my-little-but-beautiful-TIERRITA',
    project: ["Tierrita",{img:['01','02','03','04','05','06','07','08','09']}, 'Muchas gracias'],

}, {
    name: 'Charlie Indomable',
    desc: 'El unicornio filantropo del planeta Party Horse. Estudiante de intercambio que llega para alegrar y liberar del sufrimiento y tristeza de los finales a los estudiante',
    img: 'charlie.png',
    link: 'https://www.behance.net/gallery/65258033/Charlie-Indomable-Campana-multiplataforma-antiestrs',
    project: null
}, {
    name: 'Nukak',
    desc: 'Tienda especializada en venta de artesanas. Versin movil y versin web',
    img: 'Nukak.png',
    link: 'https://www.behance.net/gallery/36154343/Nukak-Web-responsive',
    project: ["Nukak",{img:['05']},'Muchas gracias'],

}, {
    name: 'Amnista Internacional',
    desc: 'Amnista internacional, un movimiento mundial con socios y socias que se toman las injusticia de manera personal para defender los derechos humanos de lxs dems personas',
    img: 'Amnistia.jpg',
    link: 'https://www.behance.net/gallery/35640283/Amnesty-International-Donation-compain',
    project: ['Amnistia',{img:['1','2','3','4']},'Muchas gracias']

}, {
    name: 'Hoy es diseo 2018-2',
    desc: 'Una plataforma de encuentro entre dinamizadores del sector creativo: estudiantes, profesionales, empresarios y creativos independientes',
    img: 'HED.png',
    link: 'https://www.hoyesdiseno.com/evento/',
    project:null
}, {
    name: 'Marca pas Marruecos',
    desc: 'Marruecos es un pas lleno de hermosas ciudades, con edificaciones detalladamente elaboradas, en las cuales resaltan la precisin y simetra que hacen parte de la gran mayora de sus mejores construcciones, convirtindose en practicamente en un arte. Es por eso que en este proyecto se quiere resaltar las cualidades que tiene su arquitectura, reflejadas en las letras identificadoras de pas,  tomando como referencia sus ms famosas construcciones .',
    img: 'Marruecos.png',
    link: 'https://www.behance.net/gallery/34422641/Nation-branding-Morocco',
    project:null
}]

interface Props{
    changePage:any
}

export const Grid = (props:Props) => {
    return <div className="grid">
        {
            projects.map((value,index) => {
                return <a key={value.name} onClick={()=>{if(value.project!=null)props.changePage(index);else{window.open(value.link,'_blank')}}}>
                <div className="item">
                    {value.project != null? <div className="paged"></div> : null}
                    <img src={"./assets/img/" + value.img} alt="" />
                    <h1>{value.name}</h1>
                    <p>{value.desc}</p>    
                </div>
                </a>
            })
        }
    </div>;
}